@import url(https://fonts.googleapis.com/css2?family=Fredoka+One&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


body {
  background-image: url(/pomodoro-timer/static/media/tomatoes3.dbdaa510.jpg);
  background-size: cover;
}

.button {
  background-color: #eee7e7;
  font-size: 14px;
  border-radius: 4px;
  border: 2px solid #5a3f3f;
  font-family: 'Abel', sans-serif;
}

#title {
  font-family: 'Fredoka One', cursive;
  font-size: xx-large;
  text-align: center;
  color: white;
}

#timer-label-container {
  position: relative;
  background-color: red;
  color: white;
  width: 400px;
  height: 200px;
  margin: 0 auto;
  padding: 30px;
  box-shadow: 5px 10px;
  font-family: 'Fredoka One', cursive;
  border-radius: 20px 20px 20px 20px;
}

#timer-label {
  text-align: center;
  font-size: 20px;
}

#time-left {
  text-align: center;
  font-size: 40px;
}

#timer-buttons {
  margin: 0;
  position: absolute;
  left: 35%;
  bottom: 5px;
}

#session-label {
  position: absolute;
  background-color: red;
  width: 200px;
  height: 100px;
  left: 100px;
  font-family: 'Fredoka One', cursive;
  color: white;
  border-radius: 20px 20px 20px 20px;
  font-size: 15px;
  box-shadow: 5px 10px;
}

#session-title {
  text-align: center;
}

#session-length {
  text-align: center;
}

#session-buttons {
  margin: 0;
  position: absolute;
  left: 40%;
  bottom: 5px;
}

#break-label {
  position: absolute;
  background-color: red;
  width: 200px;
  height: 100px;
  right: 100px;
  font-family: 'Fredoka One', cursive;
  color: white;
  border-radius: 20px 20px 20px 20px;
  font-size: 15px;
  box-shadow: 5px 10px;
}

#break-title {
  text-align: center;
}

#break-length {
  text-align: center;
}

#break-buttons {
  margin: 0;
  position: absolute;
  left: 40%;
  bottom: 5px;
}

@media (max-width: 750px) {
  #break-label {
    right: 50px
  }
  #session-label {
    left: 50px
  }
}

@media (max-width: 400px) {
  #break-label {
    right: 10px
  }
  #session-label {
    left: 10px
  }
}
